/*------ Typography Style Start ------*/
body {
  color: #444444;
  line-height: 1.7;
  font-size: 14px;
  font-weight: 400;
  background-color: #f1f1f1;
  font-family: "Roboto", sans-serif;
}

a {
  transition: 0.4s;
}

a:hover, a:focus {
  outline: none;
  text-decoration: none;
}

p {
  margin-bottom: 8px;
}

p:last-child {
  margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333333;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

strong,
b {
  font-weight: 700;
}

figure {
  margin: 0;
}

img {
  max-width: 100%;
  transition: 0.4s;
}

.bg-img {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.btn,
button {
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  box-shadow: none;
  transition: 0.4s;
  background-color: transparent;
}

.btn:active, .btn:focus,
button:active,
button:focus {
  outline: none;
  box-shadow: none;
}

input,
textarea {
  resize: none;
}

input:focus,
textarea:focus {
  outline: none;
}

::-moz-selection {
  color: #fff;
  background: #dc4734;
}

::selection {
  color: #fff;
  background: #dc4734;
}

::placeholder {
  color: #999;
  font-size: 13px;
  opacity: 1;
}

/*Custom Scroll (Perfect Scroll)*/
.custom-scroll {
  position: relative;
  overflow: hidden;
}

.custom-scroll.ps .ps__rail-y,
.custom-scroll.ps .ps__rail-x {
  background-color: transparent;
}

.custom-scroll.ps .ps__rail-y [class*="ps__thumb"],
.custom-scroll.ps .ps__rail-x [class*="ps__thumb"] {
  transition: all 0.3s ease;
}

.custom-scroll.ps .ps__rail-y {
  width: 3px;
  right: 2px;
}

.custom-scroll.ps .ps__rail-y .ps__thumb-y {
  width: 100%;
  right: 0;
}

.custom-scroll.ps .ps__rail-x {
  height: 6px;
  bottom: 3px;
}

.custom-scroll.ps .ps__rail-x .ps__thumb-x {
  height: 100%;
}

