/* ----scroll to top css start ----*/
.scroll-top {
  bottom: 80px;
  cursor: pointer;
  height: 50px;
  position: fixed;
  right: 20px;
  text-align: center;
  width: 50px;
  z-index: 9999;
  transition: 0.4s;
  border-radius: 50%;
  background-color: #dc4734;
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

